.profile__name {
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 750px) {
  .profile__name {
    text-align: center;
  }
}

@media screen and (max-width: 600px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
  }
}
